import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  AbstractControl,
  FormArray,
} from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router, ActivatedRoute } from "@angular/router";
import { BehaviorSubject, Subject, takeUntil, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { SuppliersService } from "src/app/erp-suppliersmanagement/data/supplier-maintenance.service";

import * as XLSX from "xlsx";

import {
  SelectedFiles,
  FilesService,
} from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { SupplierCategoriesLookupComponent } from "src/app/erp-procurement/modules/parameters/pages/lookups/supplier-categories-lookup/supplier-categories-lookup.component";

@Component({
  selector: "app-manage-supplier",
  templateUrl: "./manage-supplier.component.html",
  styleUrls: ["./manage-supplier.component.scss"],
})
export class ManageSupplierComponent implements OnInit {
  panelOpenState = false;
  step = 0;
  setStep(index: number) {
    this.step = index;
  }
  nextStep() {
    this.step++;
  }
  prevStep() {
    this.step--;
  }

  showForm = false;
  showVerificationDet = false;
  pageFunction = "Add";
  mngForm: FormGroup;
  currentUser: any;

  bgtForm: FormGroup;

  displayedColumns: string[] = [
    "id",
    "supplierCategoryName",
    "supplierCategoryCode",
    "actions",
  ];

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  formData: any;
  public selectedFiles: SelectedFiles[] = [];
  public isFileLoading = new BehaviorSubject(false);
  currFile: string;
  currFilename: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  hideSubmit = false;
  disableActions = false;
  supplierId: any;

  prequalificationStatuses: any[] = [{ name: true }, { name: false }];

  constructor(
    private fb: FormBuilder,
    private filesService: FilesService,
    private tokenStorageService: TokenStorageService,
    private suppliersService: SuppliersService,
    private snackbar: SnackbarService,
    private router: Router,
    private route: ActivatedRoute,
    private dialog: MatDialog
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  ngOnInit(): void {
    this.getPage();
    this.showForm = true;

    if (
      this.route.queryParams &&
      typeof this.route.queryParams.subscribe === "function"
    ) {
      this.route.queryParams.subscribe((params) => {
        if (params.hasOwnProperty("id")) {
          const action = params["action"];
          if (action == "Update") {
            this.pageFunction = "Update";
          } else if (action == "View") {
            this.pageFunction = "View";
            this.disableActions = true;
          }

          this.supplierId = params["id"];
          //const serializedData = params["additionalData"];

          this.getDataById();
        }
      });
      this.showForm = true;
    } else {
      this.showForm = true;
    }
  }
  getDataById() {
    const params = new HttpParams().set("id", this.supplierId);
    this.suppliersService
      .getSuppliersById(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);
          if (res.entity) {
            this.formData = res.entity;
            this.showVerificationDet = true;
            this.getPage();

            // this.isLoading = false;
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }
  getPage(): void {
    if (this.pageFunction === "Add") {
      this.generateEmptyForm();
    } else if (this.pageFunction === "Update") {
      this.generateFormWithData();
      this.dataSource.data = this.formData.categories;
      this.refreshTable();
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.dataSource.data = this.formData.categories;
      this.refreshTable();
      this.hideSubmit = true;
    }
  }

  generateEmptyForm() {
    this.mngForm = this.fb.group({
      companyName: ["", [Validators.required]],
      contactPerson: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      phone: ["", [Validators.required]],
      address: ["", [Validators.required]],
      categories: [[], [Validators.required]],
      prequalified: [true, [Validators.required]],
      description: ["", [Validators.required]],
      mainLocation: ["", [Validators.required]],
    });
  }

  generateFormWithData() {
    this.mngForm = this.fb.group({
      id: [this.formData.id],
      companyName: [this.formData.companyName, [Validators.required]],
      contactPerson: [this.formData.contactPerson, [Validators.required]],
      email: [this.formData.email, [Validators.required, Validators.email]],
      phone: [this.formData.phone, [Validators.required]],
      address: [this.formData.address, [Validators.required]],
      categories: [this.formData.categories, [Validators.required]],
      prequalified: [this.formData.prequalified],
      description: [this.formData.description, [Validators.required]],
      mainLocation: [this.formData.mainLocation, [Validators.required]],
    });
  }

  generateDisabledFormWithData() {
    this.mngForm = this.fb.group({
      companyName: [{ value: this.formData.companyName, disabled: true }],

      contactPerson: [{ value: this.formData.contactPerson, disabled: true }],
      email: [
        { value: this.formData.email, disabled: true },
        [Validators.required],
      ],
      phone: [
        { value: this.formData.phone, disabled: true },
        [Validators.required],
      ],
      address: [
        { value: this.formData.address, disabled: true },
        [Validators.required],
      ],
      categories: [
        { value: this.formData.categories, disabled: true },
        [Validators.required],
      ],
      prequalified: [{ value: this.formData.prequalified, disabled: true }],
      description: [{ value: this.formData.description, disabled: true }],
      mainLocation: [{ value: this.formData.mainLocation, disabled: true }],
    });
  }

  // **************************************************************************************************
  isLoading: boolean = false;
  onSubmit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.isLoading = true;
    if (this.pageFunction == "Add") {
      const arrayOfObjects: any[] = [];
      arrayOfObjects.push(this.mngForm.value);
      console.log("Array of objects", arrayOfObjects);
      this.suppliersService
        // .addSuppliers([this.mngForm.value])
        .addSuppliers(arrayOfObjects)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 201) {
              this.snackbar.showNotification(
                "snackbar-success",
                "Supplier added successfully"
              );
              this.router.navigate([
                "/erp-suppliers-management/suppliers-maintenance/all-suppliers",
              ]);
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
                // "Error creating supplier"
              );

              console.log("Error", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {},
        }),
        Subscription;
    } else if (this.pageFunction == "Update") {
      this.suppliersService
        .updateSuppliers(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res :", res);
            if (res) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-suppliers-management/suppliers-maintenance/all-suppliers",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.isLoading = false;
          },
        }),
        Subscription;
    }
  }

  cancel() {
    this.router.navigate([
      "/erp-suppliers-management/suppliers-maintenance/all-suppliers",
    ]);
  }


  onSubmitBulk() {
    console.log("this.bulk: ", this.items);
    this.isLoading = true;
    if (this.pageFunction == "Add") {
      
      this.suppliersService
        // .addSuppliers([this.mngForm.value])
        .addBulkSuppliers(this.items)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 201) {
              this.snackbar.showNotification(
                "snackbar-success",
                "Suppliers added successfully"
              );
              this.router.navigate([
                "/erp-suppliers-management/suppliers-maintenance/all-suppliers",
              ]);
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
                // "Error creating supplier"
              );

              console.log("Error", res);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            console.log("Error", err);
          },
          complete: () => {},
        }),
        Subscription;
    } 
  }
  
  //****************************************************************************************************************** */
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  supplierCategoryIsSelected = false;
  manageSupplierCategories(row, action) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "600px";
    dialogConfig.data = {
      action: "multiple supplier category",
    };

    const dialogRef = this.dialog.open(
      SupplierCategoriesLookupComponent,
      dialogConfig
    );

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.supplierCategoryIsSelected = true;

        // Loop through result.data items
        result.data.forEach((selectedCategory) => {
          const { supplierCategoryCode, supplierCategoryName } =
            selectedCategory;

          // Find matching item in dataSource.data
          const existingItemIndex = this.dataSource.data.findIndex(
            (item) =>
              item.supplierCategoryCode === supplierCategoryCode ||
              item.supplierCategoryName === supplierCategoryName
          );

          // Update existing item or create a new one
          if (existingItemIndex !== -1) {
            // Update existing item with specific properties
            this.dataSource.data[existingItemIndex] = {
              ...this.dataSource.data[existingItemIndex], // Preserve other properties
              supplierCategoryCode,
              supplierCategoryName,
            };
          } else {
            // Create a new item with only the necessary properties
            this.dataSource.data.push({
              supplierCategoryCode,
              supplierCategoryName,
            });
          }
        });

        this.refreshTable();
      }
    });
  }

  refreshTable() {
    this.dataSource.data = [...this.dataSource.data];
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.mngForm.patchValue({
      categories: this.dataSource.data,
    });

    console.log("this.dataSource.data:: ", this.dataSource.data);
  }

  deleteRecord(index: number) {
    this.dataSource.data.splice(index, 1);
    this.refreshTable();
  }

  private readonly templateFilename = "ImportSuppliersTemplate.xlsx";

  downloadTemplate() {
    const link = document.createElement("a");
    link.setAttribute("type", "hidden");
    link.href = `/assets/templates/${this.templateFilename}`;
    link.download = this.templateFilename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  // *************************************************************************************************************************************
  @ViewChild("fileInput") fileInput: any;
  importExcel() {
    // this.enterManually = false;
    this.fileInput.nativeElement.click();
  }

  items: any[] = []; // Array to store
  itemsForm: FormGroup;
  itemErrors: { [key: string]: string }[] = [];

  handleFileInput(files: FileList) {
    const file = files.item(0);

    if (file) {
      // Check if the selected file is an Excel file
      if (
        file.type ===
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" || // .xlsx
        file.type === "application/vnd.ms-excel"
      ) {
        // .xls
        // Proceed with your logic for handling the Excel file
        console.log("Excel file selected:", file);

        const fileReader = new FileReader();
        fileReader.onload = (e: any) => {
          const arrayBuffer = e.target.result;
          const workbook = XLSX.read(arrayBuffer, { type: "array" });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const jsonData = XLSX.utils.sheet_to_json(worksheet, { raw: true });

          this.items = jsonData;

          console.log(" this.items :", this.items);

          // Initialize the form
          this.itemsForm = this.fb.group({
            items: this.fb.array([]), // Array of items
          });

          // Call the function to validate the items
          this.validateItems();
        };

        fileReader.readAsArrayBuffer(file);
      } else {
        // Display an error message or take appropriate action for non-Excel files

        this.snackbar.showNotification(
          "snackbar-danger",
          "Invalid file format. Please select an Excel file."
        );
        console.error("Invalid file format. Please select an Excel file.");
      }
    }
  }

  validateItems() {
    this.itemErrors = []; // Clear previous errors
    const items = this.items;

    const itemsFormArray = this.itemsForm.get("items") as FormArray; // Create a separate FormArray variable

    for (const item of items) {
      const formGroup = this.fb.group({
        companyName: [item.companyName, [Validators.required]],
        contactPerson: [item.contactPerson, [Validators.required]],
        email: [item.email, [Validators.required, Validators.email]],
        phone: [item.phone, [Validators.required]],
        address: [item.address, [Validators.required]],
        prequalified: [
          item.prequalified,
          [Validators.required, Validators.pattern(/^(true|false)$/i)],
        ],
        description: [item.description, [Validators.required]],
        mainLocation: [item.mainLocation, [Validators.required]],
        category: [item.category, [Validators.required]],
      });

      itemsFormArray.push(formGroup); // Use push method on the FormArray variable
      this.collectErrors(formGroup);
    }
  }

  collectErrors(control: AbstractControl, path: string = "") {
    if (control instanceof FormGroup) {
      Object.keys(control.controls).forEach((key) => {
        const subControl = control.get(key);
        const subPath = this.getErrorPath(path, key);
        this.collectErrors(subControl, subPath);
      });
    } else {
      const errors = control.errors;
      if (errors) {
        const itemError = {
          field: path,
          message: this.getErrorMessage(errors),
        };
        this.itemErrors.push(itemError);
      }
    }
  }

  getErrorPath(path: string, key: string): string {
    return path ? `${path}.${key}` : key;
  }

  getErrorMessage(errors: any): string {
    if (errors.required) {
      return "This field is required.";
    } else if (errors.pattern) {
      return "Invalid value.";
    }
    return "";
  }

  addImportsToArray() {}
  clearImports() {}

  //***********************************************************************************************************************************8 */
}
