import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class SupplierCategoriesService {

  constructor(private http: HttpClient) { }
  
  getCategories(): Observable<any> {
    const SupplierCatUrl = `${environment.baseUrlProcurement}/api/v1/supplierCategory/view`;
    return this.http.get<any[]>(SupplierCatUrl);
  }
  getCategoriesById(id: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/supplierCategory/view_byId/${id}`);
  }


  addCategories(data: any): Observable<any> {
    const SupplierCatUrl = `${environment.baseUrlProcurement}/api/v1/supplierCategory/add`;
    return this.http.post<any>(SupplierCatUrl, data);
  }

  updateCategories(CategoryDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/supplierCategory/update/`;
    return this.http.put<any>(updateBillUrl, CategoryDetails)
  }


  deleteCurrencyPemanently(id: any): Observable<any> {
    const SupplierCatUrl = `${environment.baseUrlProcurement}/api/v1/supplierCategory/delete/${id}`;
    return this.http.delete<any>(SupplierCatUrl, id);
  }


    // ********************************************************************************************************************



}
