import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllSuppliersComponent } from './all-suppliers/all-suppliers.component';
import { ManageSupplierComponent } from './manage-supplier/manage-supplier.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';


const routes: Routes = [
   //Budgets
   {
    path: "all-suppliers",
    component: AllSuppliersComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'SuppliersManagementModule', requiredPrivilege:["All Suppliers"] },
  },
 
  {
    path: "manage-supplier",
    component: ManageSupplierComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'SuppliersManagementModule', requiredPrivilege:["Manage Supplier"] },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SuppliersMaintenanceRoutingModule { }
